.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
    box-shadow: 2px 5px 5px #fff;
}
.header-bg{
    background-color: rgba(0,0,0,0.9);
    transition: 0.5s;
}
.nav-menu{
    display: flex;
}
.nav-menu li{
    padding: 0 1rem;
}
.nav-menu li a::after{
    content: "";
    height: 2px;
    background-color: #fff;
    display: block;
    transition: 0.2s;  
    width: 0;  
}
.nav-menu li a:hover::after{
    width: 100%;
}
.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.hambuger{
    display: none;
}
@media screen and (max-width:1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .hambuger{
        display: initial;
    }
}